.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


#root,
.ant-layout,
.ant-layout-content,
.h-100
{
  height: 100%;
}

#root .gallery-text-600{
    text-align: center;
    font-size: x-large;
    font-weight: 600;
    letter-spacing: 1px;
    background-color: #fff;
    box-shadow: 2px 2px 7px #cecece;
}

.d-flex-align-center{
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  overflow: hidden;
}

.gallery-content-com-0925{
  overflow: auto;
  height: 100%;
}

.parent-gallery-div-09067e{
    display: inline-flex;
    flex-wrap: wrap;
    gap: 10px;
    align-items: center;
    justify-content: center;
    padding: 25px;
}

.parent-gallery-div-09067e img{
  height: 200px;
  object-fit: cover;
}

.parent-gallery-div-09067e .child-img-div-1011{
  background: #fff;
  box-shadow: 2px 2px 7px #cecece;
  display: flex;
  align-items: center;
  padding: 4px;
}

